body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  height: 100vh;
  align-items: center;
  align-content: center;
  justify-content: center;
  background-color: darkolivegreen;
}
#calculator {
  width: 400px;
  margin: auto;
  height: 500px;
  border: 2px solid gray;
  border-radius: 2px;
  background-color: black;
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-top: 20px;
}
#displays {
  width: 100%;
  height: 75px;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: 24px;
}
#output {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  color: darkgoldenrod;
}
#display {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
#buttons {
  width: 98%;
  color: white;
  margin: 10px auto;
  height: 80%;
  display: grid;
  grid-template-areas:
    "ac ac div mul"
    "seven eight nine minus"
    "four five six plus"
    "one two three equ"
    "zero zero dot equ";
  grid-gap: 2px;
  background-color: grey;
}

.button {
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
}
